* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Kanit', sans-serif;
}

.navbar {
    background: linear-gradient(50deg, rgb(255, 255, 255) 0%, rgb(255, 0, 0) 100%);
    height: 20vh;
    margin: 0 auto;
    max-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: .9rem;
    font-weight: bold;
    font-family: 'Kanit', sans-serif;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 999;
    margin-bottom: 30px;
}

.navbar .imglogo {
    margin: 100px;
}

.navbar-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    max-width: 1140px;
    margin: 0 auto;
    transform: translate(115px, 0px);
}

.nav-links:hover {
    background-color: rgb(255, 247, 0);
    color: rgb(255, 0, 0);
    font-weight: bold;
}

.nav-menu {
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 0;
    list-style: none;
    text-align: center;
    justify-content: end;
    margin-right: 5rem;
    text-transform: uppercase;
}

.nav-item {
    height: 80px;
}

.nav-item ul {
    font-size: 0.8em;
    position: absolute;
    display: none;
    width: inherit;
}

.nav-item ul:hover a {
    display: block;
}

.nav-links {
    color: rgb(255, 255, 255);
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
}

.nav-links:hover {
    border-bottom: 4px solid rgb(16, 2, 2);
    transition: all 0.2s ease-out;
}

.nav-item button {
    background-color: #ff110000;
    border: none;
    color: white;
    text-align: center;
    font-size: 16px;
}

.fa-bars {
    color: rgb(0, 0, 0);
}

.nav-links-mobile {
    display: none;
}

.menu-icon {
    display: none;
}

@media screen and (max-width: 960px) {
    .NavbarItems {
        position: relative;
    }
    .img-grid img {
        padding-right: 10px;
        border-radius: 40px;
        transform: translate(-5px, -70px);
    }
    .nav-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 10vh;
        position: absolute;
        bottom: 80vh;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
        background-color: aqua;
    }
    .nav-menu.active {
        background: #000000;
        left: 0;
        opacity: 1;
        position: inherit;
        height: -webkit-fill-available;
        transition: all 0.5s ease;
        /* z-index: 1; */
    }
    .nav-links {
        text-align: center;
        padding: 2rem;
        width: 100%;
        display: table;
        background-color: rgb(0, 0, 0);
    }
    .nav-links:hover {
        background-color: #fff;
        color: #242424;
        border-radius: 0;
    }
    .navbar-logo {
        position: absolute;
        top: 0;
        left: 0;
        transform: translate(-320%, 0);
        font-size: 1.2rem;
    }
    .menu-icon {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
    }
    .fa-times {
        color: rgb(221, 255, 0);
        font-size: 2rem;
        padding-right: 80px;
    }
    .imglogo {
        display: inline;
    }
    .nav-links-mobile {
        /* display: block; */
        text-align: center;
        margin: 2rem auto;
        border-radius: 4px;
        width: 80%;
        text-decoration: none;
        font-size: 1.5rem;
        background-color: transparent;
        color: rgb(16, 2, 2);
        padding: 14px 20px;
        border: 1px solid #fff;
        transition: all 0.3s ease-out;
    }
    .nav-links-mobile:hover {
        background: #fff;
        color: #ffffff;
        transition: 250ms;
    }
    .dropdown {
        position: relative;
        display: inline-block;
    }
}

