    position: relative;
    margin: 50px 0 45px;
}

.cards__item {
    display: inline-block;
    flex: 1 1;
    margin: 0 1rem;
    border-radius: 10px;
}

.cards__item__link {
    display: flex;
    flex-flow: column;
    padding: 10px;
    width: 100%;
    box-shadow: 0 6px 20px rgb(5, 5, 5);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    border-radius: 10px;
    overflow: hidden;
    text-decoration: none;
}

.cards__item__pic-wrap {
    position: relative;
    width: 85%;
    padding-top: 100%;
    overflow: hidden;
}

.fade-img {
    -webkit-animation-name: fade-img;
            animation-name: fade-img;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
}

.cards__item__pic-wrap::after {
    content: attr(data-category);
    position: -webkit-sticky;
    position: sticky;
    text-transform: uppercase;
    bottom: 0;
    margin-left: 10px;
    padding: 10px 10px;
    max-width: calc((100%) - 60px);
    font-size: 0.8rem;
    font-weight: bold;
    color: rgb(255, 255, 255);
    background-color: #ff0000;
    box-sizing: border-box;
}

.cards__item__img {
    margin: auto;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 120%;
    max-width: 100%;
    height: auto;
    max-height: 100%;
    object-fit: cover;
    transition: all 0.2s linear;
    padding: 10px;
}

.cards__item__img:hover {
    transform: scale(1.1);
}

.cards__item__info {
    padding: 20px 130px 30px;
    text-align: center;
}

.cards__item__text {
    color: #ffffff;
    background: linear-gradient(90deg, rgb(0, 194, 253) 0%, rgb(0, 8, 255) 100%);
    font-size: 1rem;
    line-height: 1.5rem;
    font-family: 'Kanit', sans-serif;
}

