        font-size: 50px;
        margin-top: -100px;
    }
    .hero-container>p {
        font-size: 30px;
    }
    .btn-mobile {
        display: block;
        text-decoration: none;
    }
    .btn {
        width: 100%;
    }
    .hero-img-container {
        max-width: 375px;
        margin: 0 auto;
    }
    .navbar-logo {
        position: absolute;
        /* top: 0; */
        /* left: 0; */
        transform: translate(-240%, 25%);
        font-size: 1.2rem;
    }
    .navbar {
        background: linear-gradient(90deg, rgb(255, 255, 255) 0%, rgb(99, 202, 225) 100%);
        height: 80px;
        width: 480px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 0.9rem;
        font-weight: bold;
        font-family: 'Kanit', sans-serif;
        position: -webkit-sticky;
        position: sticky;
        top: 0;
        z-index: 999;
        transform: translate(5px, 0px);
    }
    .navbar-container {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 80px;
        max-width: 100%;
        transform: translate(-80px, 0px);
    }
    .fa-times {
        color: rgb(221, 255, 0);
        font-size: 2rem;
        padding-right: 20px;
    }
    .fa-bars:before {
        content: "\f0c9";
        padding-right: 100px;
    }
    .hero-img-container .dataText {
        padding-top: 4vh;
        text-align: center;
        font-size: 1rem;
        font-weight: 500;
        color: rgb(255, 0, 0);
        font-family: 'Kanit', sans-serif;
    }
    .hero-img-container .data__Text {
        padding-top: 6vh;
        text-align: center;
        font-size: 1rem;
        font-weight: 500;
        color: rgb(0, 60, 255);
        font-family: 'Kanit', sans-serif;
    }
}

@media screen and (max-width: 425px) {
    .hero-container {
        height: 40vh;
    }
    .hero-container .img-home {
        max-width: 425px;
    }
}
.footer-container {
    background: linear-gradient(0deg, rgb(255, 255, 255) 0%, rgb(255, 0, 0) 100%);
    justify-content: center;
    align-items: center;
    margin: 0 auto;
}

.footer-subscription-heading {
    margin-bottom: 30px;
    padding: 30px;
    font-size: 14px;
    text-align: left;
    text-indent: 50px;
    font-family: 'Kanit', sans-serif;
    line-height: 1.5;
}

.footer-links {
    max-width: 1140px;
    margin: 0 auto;
    display: flex;
    padding: 50px;
    justify-content: center;
}

.footer-link-items span {
    display: grid;
    font-family: 'Kanit', sans-serif;
    margin: 10px;
    font-size: 14px;
    text-align: left;
    width: 50vh;
    box-sizing: border-box;
    line-height: 1.5;
}

.footer-link-items h2 {
    margin-bottom: 10px;
    text-align: left;
    text-transform: uppercase;
}

.footer-link-items>h2 {
    color: rgb(255, 255, 255);
}

.footer-link-items a {
    color: rgb(255, 255, 255);
    text-decoration: none;
    margin-bottom: 0.5rem;
}

.footer-link-items a:hover {
    color: rgb(255, 255, 255);
    transition: 0.3s ease-out;
}

.website-rights {
    color: rgb(255, 255, 255);
    margin-bottom: 16px;
    font-weight: bold;
    font-size: 20px;
    font-family: 'Kanit', sans-serif;
}

.footer-link-items {
    color: white;
}

.map {
    padding: 40px;
}

@media screen and (max-width: 414px) {
    .footer-links {
        max-width: 1140px;
        margin: 0 auto;
        /* display: flex; */
        display: grid;
        grid-template-columns: 1fr;
        padding: 50px;
        justify-content: center;
    }
}

@media screen and (max-width: 768px) {
    .footer-links {
        max-width: 1140px;
        margin: 0 auto;
        /* display: flex; */
        display: grid;
        grid-template-columns: 1fr;
        padding: 50px;
        justify-content: center;
    }
}

@media screen and (max-width: 1024px) {
    .footer-links {
        max-width: 1024px;
        margin: 0 auto;
        display: grid;
        padding: 50px;
        justify-content: center;
    }
}

@media screen and (max-width: 425px) {
    .footer-links {
        max-width: 425px;
        margin: 0 auto;
        display: grid;
    }
    .footer-link-items>h2 {
        color: rgb(255, 255, 255);
        text-align: center;
    }
    .footer-link-items a {
        color: rgb(255, 255, 255);
        text-decoration: none;
        margin-bottom: 0.5rem;
        text-align: center;
    }
    .footer-link-items {
        color: white;
        text-align: center;
    }
    .footer-link-items>span {
        max-width: 425px;
        color: rgb(255, 255, 255);
        text-align: center;
    }
    .footer-container {
        max-width: 425px;
    }
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.item-containers {
    max-width: 1140px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    margin: 0 auto;
}

ul.pagination {
    display: inline-block;
}

ul.pagination li {
    display: inline;
}

ul.pagination li a {
    color: black;
    float: left;
    padding: 8px 16px;
    text-decoration: none;
}

.containers {
    max-width: 1140px;
    margin: 0 auto;
    background-color: rgb(255, 255, 255);
    margin-bottom: 50px;
    margin-top: 50px;
}

.containers span {
    font-weight: bold;
    padding: 0 25%;
    font-size: smaller;
}

.btn-next-page {
    padding-top: 65px;
    padding-bottom: 20px;
}

.data-one {
    color: red;
    text-Indent: 1rem;
    text-decoration: underline;
    font-weight: bold;
    font-family: 'Kanit', sans-serif;
    padding: 12px;
    font-size: .9rem;
}

.data-two {
    color: black;
    text-Indent: .2rem;
    font-Size: .5rem;
    padding: 12px;
    font-family: 'Kanit', sans-serif;
}

.img {
    transition: transform .2s;
    margin: 0 auto;
}

.btn-btn .btn-btn-2 {
    align-items: center;
    padding: 15px 20px;
    margin: 10px;
    color: white;
    background-color: red;
}

.datanumber {
    color: rgb(122, 83, 83);
    font-weight: bold;
    font-family: 'Kanit', sans-serif;
    text-align: center;
    transform: translate(-100px, 35px);
}

.data-two:hover {
    font-size: 1rem;
    text-align: left;
}

.data-one:hover {
    font-size: 1rem;
    text-align: left;
}

.img:hover {
    transform: scale(1.2);
}

